import { useState, useEffect } from 'react';
//import Reveal from '../libs/reveal.js/dist/reveal.js';
//import Markdown from '../libs/reveal.js/plugin/markdown/markdown.esm.js';

export const reveal_initialize = () => {
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {

    const Reveal = require('../libs/reveal.js/dist/reveal.js');
    const Markdown = require('../libs/reveal.js/plugin/markdown/markdown.esm.js');

    function init() {
      let deck = new Reveal({
        plugins: [ Markdown ]
      })
        deck.initialize({});
    }

    if (isInitialized === false) {
        init();
        setIsInitialized(true);
    }
    console.log("Effect hook is working")
  });

  if (isInitialized === false) {
    return 'Loading...';
  }
  return isInitialized ? true : false;

}

